.apartments {
  margin: 0 16px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 340px);
  row-gap: 48px;
  column-gap: 20px;
  justify-content: center;
}

@media (min-width: 768px) {
  .apartments {
    grid-template-columns: repeat(auto-fit, 412px);
    margin: 0 82px;
  }
}